import meSideMenu from "@/components/meSideMenu";
import { JOBS_APPLICATIONS } from "@/api/api";
import defaultAvatar from '@/assets/default/avatar.png';
export default {
  name: "applications",
  components: {
    meSideMenu
  },

  data() {
    return {
      dialogTableVisible: false,
      defaultAvatar,
      applicationData: [],
      educatorData: [],
      page: 1,
      limit: 10,
      totalNum: 0,
      jobId: 0
    };
  },

  mounted() {
    this.jobId = this.$route.query.id;
    this.getJobResumes(this.jobId, this.page, this.limit);
  },

  methods: {
    turnEducatorProfile(educatorData) {
      console.log(educatorData);
      this.dialogTableVisible = true;
      this.educatorData = educatorData;
    },

    getJobResumes(jobId, page, limit) {
      let params = {
        page: page,
        limit: limit,
        job_id: jobId
      };
      JOBS_APPLICATIONS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.applicationData = res.message.data; // console.log(res.message.data)

          this.totalNum = res.message.total;
        }
      }).catch(err => {
        console.log(err);
      });
    },

    pageSizeChange(e) {
      console.log(e);
    },

    pageChange(e) {
      this.page = e;
      this.getJobResumes(e, this.limit);
    },

    turnInfoPerson(id) {
      this.$router.push({
        path: '/info/personal',
        query: {
          id: id,
          identity: 1
        }
      });
    }

  }
};